import React from 'react';
import './Enquiry.css';
import Header from '../header/Header';
import Form from 'react-bootstrap/Form';
import logo from '../../assets/images/logo.png';
import { Button } from 'react-bootstrap';

function Enquiry() {
    return (
        <div>
            <Header />
            <div className='enquiry-heading'>
                <h1>Enquire Now for Frachising with Prime Fresh Limited</h1>
            </div>
            <div className='enquiry-container'>
                <div className='enquiry-images'>
                    <img src={logo} alt='' />
                </div>
                <div className='enquiry-form'>
                    <Form>
                        <Form.Group className="mb-3" controlId="formGroupEmail">
                            <Form.Label>Name</Form.Label>
                            <Form.Control type="text" placeholder="" required />
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="formGroupPassword">
                            <Form.Label>Email</Form.Label>
                            <Form.Control type="email" placeholder="" required />
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="formGroupPassword">
                            <Form.Label>Phone Number</Form.Label>
                            <Form.Control type="number" placeholder="" required />
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="exampleForm.ControlTextarea1">
                            <Form.Label>Address</Form.Label>
                            <Form.Control as="textarea" rows={3} />
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="formGroupPassword">
                            <Form.Label>Current Occupation</Form.Label>
                            <Form.Control type="text" placeholder="" required />
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="formGroupOccupation">
                            <Form.Label>Type of Franchise Association</Form.Label>
                            <Form.Control as="select" required>
                                <option value="" disabled>Select Franchise Association</option>
                                <option value="engineer">Single-Unit</option>
                                <option value="teacher">Multi-Unit</option>
                                <option value="doctor">Master-Franchise</option>
                            </Form.Control>
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="formGroupPassword">
                            <Form.Label>Indicative Investment(INR Lacs)</Form.Label>
                            <Form.Control type="text" placeholder="" required />
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="formGroupPassword">
                            <Form.Label>Desire Business Location</Form.Label>
                            <Form.Control type="text" placeholder="" required />
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="formGroupPassword">
                            <Form.Label>Liquid Capital Avaliable To Invest(INR Lacs)</Form.Label>
                            <Form.Control type="text" placeholder="" required />
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="formGroupPassword">
                            <Form.Label>Time Frame On Start Of Business(Months)</Form.Label>
                            <Form.Control type="text" placeholder="" required />
                        </Form.Group>
                        <Button className='enquiry-buttons' variant="primary" type="submit">
                            Submit
                        </Button>
                    </Form>
                </div>
            </div>

        </div>
    )
}

export default Enquiry