import React from 'react';
import './Contact.css';
import Form from 'react-bootstrap/Form';
import Header from '../header/Header';
import { Button } from 'react-bootstrap';
import logo from '../../assets/images/logo.png';

function Contact() {
    return (
        <div>
            <Header />
            <div className='contact-heading'>
                <h1>CONTACT US</h1>
            </div>
            <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3937.0729765159103!2d76.5371160759171!3d9.326801740746877!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3b06218371a8e90b%3A0x7cb93f3b25a8761d!2sSt.%20Gregorios%20Medical%20Mission%20Multi-Speciality%20Hospital!5e0!3m2!1sen!2sin!4v1702100636558!5m2!1sen!2sin" width="2000" height="450"></iframe>
            <div className='contact-container'>
                <div className='contact-images'>
                    <img src={logo} alt='' />
                </div>
                <div className='contact-form'>
                    <Form>
                        <Form.Group className="mb-3" controlId="formGroupEmail">
                            <Form.Label>Name</Form.Label>
                            <Form.Control type="text" placeholder="" required />
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="formGroupPassword">
                            <Form.Label>Email</Form.Label>
                            <Form.Control type="email" placeholder="" required />
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="formGroupPassword">
                            <Form.Label>Phone Number</Form.Label>
                            <Form.Control type="number" placeholder="" required />
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="formGroupPassword">
                            <Form.Label>Category</Form.Label>
                            <Form.Control type="text" placeholder="" required />
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="formGroupPassword">
                            <Form.Label>Designation</Form.Label>
                            <Form.Control type="text" placeholder="" required />
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="formGroupPassword">
                            <Form.Label>Organisation</Form.Label>
                            <Form.Control type="text" placeholder="" required />
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="formGroupPassword">
                            <Form.Label>Phone</Form.Label>
                            <Form.Control type="text" placeholder="" required />
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="formGroupPassword">
                            <Form.Label>City</Form.Label>
                            <Form.Control type="text" placeholder="" required />
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="exampleForm.ControlTextarea1">
                            <Form.Label>Address</Form.Label>
                            <Form.Control as="textarea" rows={3} />
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="exampleForm.ControlTextarea1">
                            <Form.Label>Message</Form.Label>
                            <Form.Control as="textarea" rows={3} />
                        </Form.Group>
                        <Button className='contact-buttons' variant="primary" type="submit">
                            Submit
                        </Button>
                    </Form>
                </div>
            </div>
        </div>
    )
}

export default Contact